import { SessionInfoComponent } from './../../common/session-info/session-info.component';
import { AlertService } from 'src/app/services/alert.service';
import { SessionsManagerService } from 'src/app/services/sessions-manager-service';
import { NavController, LoadingController, AlertController } from "@ionic/angular";
import { User } from "src/app/models/user";
import { Provider, ProviderStatus } from "src/app/models/provider";
import { ProviderService } from "src/app/services/provider.service";
import { ActivatedRoute } from "@angular/router";
import { RouterTestingModule } from "@angular/router/testing";
import { Component, OnInit } from "@angular/core";
import { SessionTimingType } from "src/app/models/session";
import { zip } from 'rxjs';
import { take } from 'rxjs/operators';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: "app-usr-select-session-type",
  templateUrl: "./usr-select-session-type.page.html",
  styleUrls: ["./usr-select-session-type.page.scss"],
})
export class UsrSelectSessionTypePage implements OnInit {
  provider: Provider;
  getName = User.getName;
  immediateDisabled: boolean = true;
  selectedType: SessionTimingType;
  SessionType = SessionTimingType;

  constructor(
    private route: ActivatedRoute,
    private providerService: ProviderService,
    private navController: NavController,
    private sessionsManager: SessionsManagerService,
    private loadingModal: LoadingController,
    private alert:AlertService
  ) {}

  ngOnInit() {
    var providerId = this.route.snapshot.params.providerId;
    this.provider = this.providerService.selectedProviderShared.resource();
    this.setDefaultValue();
    this.providerService.selectedProviderShared
      .getUpdateNotifier(this)
      .subscribe((provider) => {
        this.provider = this.providerService.selectedProviderShared.resource();
        this.setDefaultValue();
      });
    this.providerService.selectedProviderShared.request(providerId);
  }
  setDefaultValue() {
    if (this.provider) {
      if (this.provider.status == ProviderStatus.ONLINE) {
        this.selectedType = SessionTimingType.IMMEDIATE;
        this.immediateDisabled = false;
      } else {
        this.selectedType = SessionTimingType.RESERVATION;
        this.immediateDisabled = true;
      }
    }
  }
  submit() {
    if (this.selectedType == SessionTimingType.IMMEDIATE) {
      this.initiateStartSession();
    } else if (this.selectedType == SessionTimingType.RESERVATION) {
      this.navController.navigateForward([
        "/usr-view-provider-calendar/" + this.provider.id,
      ]);
    }
  }
  initiateStartSession() {
    zip(this.sessionsManager.selectedProviderReservedSessionsShared
      .getResourceReadyNotifier(),
      this.sessionsManager.myCurrentSessionsShared.getResourceReadyNotifier()).pipe(take(1))
      .subscribe(result=>{
        this.loadingModal.dismiss();
        let date = new Date();
        date.setHours(0,0,0);
        this.sessionsManager.reserveSession(date, null,this.provider,SessionTimingType.IMMEDIATE,SessionInfoComponent)
      })
    this.sessionsManager.myCurrentSessionsShared.request();
    this.sessionsManager.selectedProviderReservedSessionsShared.request(
      this.provider.id
    );
    this.alert.presentLoadingModal(this.loadingModal).subscribe();

    
  }
  startSession(duration: number, isTextSession) {
    if (
      this.sessionsManager.hasEnoughBalanceForSessionRequest(
        this.provider,
        isTextSession,
        duration
      )
    ) {
    }
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
  }
}
